.buy-container {
    margin-top: 20px;
    border-radius: 5px;
    -webkit-box-shadow: 1px 1px 8px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 1px 1px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 10px 20px;
}

.buy-container.collapsed {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.buy-container.expanded {
    width: 100%;
}

.buy-button {
    border: none;
    background: none;
    cursor: pointer;
}


